.NavigationItem {
    margin: 10px 0;
    box-sizing: border-box;  
    display: block;
    width: 100%;
}

.NavigationItem a {
    color: #8F5C2C;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active 
{
    color:  #40A4C8;
}

@media (min-width: 500px) {
    .NavigationItem {
        margin: 0;
        box-sizing: border-box;  
        display: flex;
        align-items: center;
        height: 100%;
    }
    
    .NavigationItem a {
        color: white;
        text-decoration: none;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
        box-sizing: border-box;
        display: block;
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active, 
    .NavigationItem a.active 
    {
    height: 110%;
    background-color: #8F5C2C;
    border-bottom: 4px solid #40A4C8; 
    color: white;
    }     
}